export default{
	data(){
		return{
			recommendSongList:[]
		}
	},
	onLoad(){
		uni.request({
			url:'https://api.qq.jsososo.com/recommend/playlist',
			success: (res) => {
				this.recommendSongList=res.data.data.list
				console.log("----获取推荐歌单数据----->",res)
			}
			
		})
	},
	methods:{
		//去推荐歌单详细页面
		torRecommendDetail(data){
			console.log("----》",data)
			uni.redirectTo({
				url:'../recommendDetail/recommendDetail?upPage=1&tid='+data.tid
			})
		}
	}
}
